import { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { FetchMovieCast } from '../../servises/movieFetch';
// import { NavLink } from 'react-router-dom';

const IMAGE_URL = 'https://image.tmdb.org/t/p/w200';
export default function Cast() {
    const { url } = useParams();
    // const movieId = match.params.url;
    const [cast, setCast] = useState(null)
    // const { credits } = useParams();


    const { movieId } = useParams();
    console.log(url)
    useEffect(() => {
        FetchMovieCast(movieId).then(response => {
            setCast(response.cast);
            console.log(response.cast.map(e => { return (e.id) }))
        })
        return () => {
        }
    }, [movieId])


    return (
        cast && (
            <ul >
                {cast.map(e => {
                    return (
                        <li key={e.id}>
                            {
                                e.profile_path ? (
                                    <img src={`${IMAGE_URL}${e.profile_path}`} alt={e.name} />
                                ) : (
                                    <img
                                        src="https://cdn-icons-png.flaticon.com/512/2748/2748558.png"
                                        alt="Not found"
                                        width={200}
                                    />
                                )}
                            <p>{e.name}</p>
                            <p>{e.character}</p>
                        </li>
                    );
                })}
            </ul >
        )
    );
}